<template>
  <section id="login">
    <form action="">
      <inputList ref="name">请用您的用户名或者手机号登录</inputList>
      <inputList ref="pw">请输入您的密码</inputList>
      <input type="submit" @click="login({userId,passWd})" class="login-input m-t-10 login-b f-c-t"
             value="登录">
      <p class="text-r p-R-20 m-t-10">
        <router-link to="./register" class="f-c-darkcyan">注册</router-link>
        <router-link to="resetPW" class="f-c-darkcyan">忘记密码</router-link>
      </p>
    </form>
  </section>
</template>
<script>
  import {mapMutations, mapState} from "vuex"
  import inputList from "@/components/loList"
  export default({
    name: 'index',
    data(){
      return {
        userId: "",
        passWd: "",
        user: this.$store.state.userState,
      }
    },
    created(){
        document.title="登录"
    },
    components: {inputList},
    computed: {...mapState(["userState"])},
    methods: {
      ...mapMutations(["login"]),
    }
  })
</script>
<style scoped>
  #login {
    max-width: 300px;
    height: 210px;
    border: 1px solid gainsboro;
    margin: 60px auto 0;
    width: 60%;
    padding: 0 10%;
    min-width: 200px;
  }

  .login-input {
    max-width: 260px;
    width: 100%;
    height: 30px;
    background: darkcyan;
    border: 0;
    border-radius: 5px;
    font-size: 16px;
    color: aliceblue;
  }

  .login-input:hover {
    background: #005a5a;
    color: white;
  }

  @media screen and (max-width: 900px) {
    #login {

    }

    .login-input {
    }
  }

</style>



