<template>
  <section id="app-index">
    <pageTop></pageTop>
    <pageSusp></pageSusp>
    <div class="app-index-list">
      <pageList class=""></pageList>
      <pageList class=""></pageList>
      <pageList class=""></pageList>
    </div>
  </section>
</template>
<script>
  import pageList from "@/components/pageList"
  import pageTop from "@/components/pageTop"
  import pageSusp from "@/components/pageSusp"
  export default({
    name: 'index',
    data(){
      return {}
    },
    created(){
      document.title = "首页"
      localStorage.tag = 1;
      sessionStorage.tag = 2;
    },
    components: {pageList, pageTop, pageSusp}
  })
</script>
<style scoped>
  #app-index {
    width: 90%;
    margin: 0 auto;
  }

  .app-index-list {
    position: relative;
    top: 50px;
  }
</style>



